import React, { useRef, useEffect } from 'react'

const drawSquare = (squareSide) => (context2d) => (x, y) => {
  context2d.beginPath()
  context2d.moveTo(x, y)
  context2d.lineTo(x + squareSide, y)
  context2d.lineTo(x + squareSide, y + squareSide)
  context2d.lineTo(x, y + squareSide)
  context2d.closePath()
  context2d.fill()
}

export default ({ width, height, devicePixelRatio }) => {
  const ref = useRef()

  useEffect(() => {
    if (ref.current !== undefined) {
      const squareSide = 10 * devicePixelRatio

      const middlePoint = [
        Math.floor(width / 2) * devicePixelRatio, 
        Math.floor(height / 2) * devicePixelRatio
      ]
      
      const context2d = ref.current.getContext('2d')
      drawSquare(squareSide)(context2d)(...middlePoint)
    }
  })

  return <canvas 
        ref={(domElement) => ref.current = domElement}
        width={width * devicePixelRatio} 
        height={height * devicePixelRatio} 
        style={{width, height, display: 'block'}} 
    />
}